<script setup>
import { ref, onMounted } from 'vue'
import draggable from 'vuedraggable'
import Button from './Button.vue'

const taskArr = ref([
	{
		task: 'Создать приказ об увольнении. В основании указать решение МВД об аннулировании визы',
		position: 1,
	},
	{
		task: 'Ознакомить сотрудника с приказом под роспись',
		position: 2,
	},
	{
		task: 'Внести запись об увольнении в трудовую книжку или сведения о трудовой деятельности',
		position: 3,
	},
	{ task: 'Уведомить МВД об увольнении сотрудника', position: 4 },

	{ task: '', position: 5 },
])

const correctForSixPosition =
	'Нужно сделать все возможное, чтобы бывший работник вовремя уехал из страны'

const isUserFail = ref(null)
const checkStatus = ref(false)
const isDraggable = ref(true)

onMounted(() => {
	shuffleTasks()
})

const shuffleTasks = () => {
	taskArr.value = taskArr.value.sort(() => Math.random() - 0.5)
}

const checkOrder = () => {
	checkStatus.value = true
	isDraggable.value = false

	// Проверка правильности порядка
	const isOrderCorrect = taskArr.value.every(
		(item, index) => item.position === index + 1
	)

	// Проверка правильности текста для 6 позиции
	const sixthElement = taskArr.value.find(item => item.position === 5)
	const isTextCorrect = sixthElement?.task?.trim() === correctForSixPosition

	isUserFail.value = !(isOrderCorrect && isTextCorrect)
}

const getBorderColor = (element, index) => {
	if (!checkStatus.value) return '0.5vw solid #ffea7e'

	const isPositionCorrect = element.position === index + 1
	let isTaskCorrect = true

	if (element.position === 5) {
		isTaskCorrect =
			element.task?.replace(/\s+/g, '').toUpperCase() ===
			correctForSixPosition.replace(/\s+/g, '').toUpperCase()
	}

	return isPositionCorrect && isTaskCorrect
		? '0.5vw solid #90ee90'
		: '0.5vw solid red'
}

const tryAgain = () => {
	// Сброс к исходному состоянию
	taskArr.value = [
		{
			task: 'Создать приказ об увольнении. В основании указать решение МВД об аннулировании визы',
			position: 1,
		},
		{
			task: 'Ознакомить сотрудника с приказом под роспись',
			position: 2,
		},
		{
			task: 'Внести запись об увольнении в трудовую книжку или сведения о трудовой деятельности',
			position: 3,
		},
		{ task: 'Уведомить МВД об увольнении сотрудника', position: 4 },

		{ task: '', position: 5 },
	]

	shuffleTasks()
	isUserFail.value = null
	checkStatus.value = false
	isDraggable.value = true
}

const reload = () => {
	location.reload()
}
</script>

<template>
	<div class="container">
		<div class="container__game">
			<draggable
				v-model="taskArr"
				class="draggable-list"
				:animation="200"
				:ghost-class="'drag-ghost'"
				item-key="position"
				:disabled="!isDraggable"
			>
				<template #item="{ element, index }">
					<div
						class="container__game_item"
						:style="{ border: getBorderColor(element, index) }"
						:data-index="index + 1"
					>
						<textarea
							v-if="element.position === 5"
							v-model="element.task"
							placeholder="Введите правильный этап"
							:disabled="!isDraggable"
							class="text-input"
							rows="2"
						></textarea>

						<span v-else>{{ element.task }}</span>
					</div>
				</template>
			</draggable>
		</div>

		<Transition mode="out-in">
			<div v-if="!isDraggable" class="container__mgs">
				<div v-if="!isUserFail" class="container__mgs-item good">
					ВЫ СПРАВИЛИСЬ С ЗАДАНИЕМ! <br />
					ПРОЙТИ ТРЕНИРОВКУ СНОВА?
				</div>
				<div v-else class="container__mgs-item bad">ОШИБКА</div>
			</div>
		</Transition>

		<div class="container__btns">
			<Transition mode="out-in">
				<Button v-if="isDraggable" @click="checkOrder">ПРОВЕРИТЬ</Button>
				<div v-else>
					<Button v-if="isUserFail" @click="tryAgain">ПОПРОБОВАТЬ СНОВА</Button>
					<Button v-else @click="reload">ПРОЙТИ СНОВА </Button>
				</div>
			</Transition>
		</div>
	</div>
</template>

<style scoped>
.container__game {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.container__game_item {
	margin: 2vw 0;
	padding: 1vw 4vw;
	width: 50vw;
	text-align: center;
	font-size: 0.95em;
	border: 0.5vw solid #ffea7e;
	background-color: #fef2b8;
	border-radius: 1vw;
	cursor: grab;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.container__game_item::before {
	content: attr(data-index);
	position: absolute;
	top: 20%;
	left: 0;
	transform: translate(-50%, -50%);
	width: 4vw;
	height: 4vw;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 1em;
	background-color: #ffea7e;
	border-radius: 50%;
	color: #000;
	font-weight: bold;
}

.drag-ghost {
	background-color: #fddb34 !important;
	opacity: 0.8 !important;
	border-color: #fddb34 !important;
}

.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}

.container__btns {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
}

.container__mgs {
	margin: 1vw 0;
	display: flex;
	justify-content: center;
}

.container__mgs-item {
	background-color: #ddebf7;
	padding: 2vw 3vw;
	max-width: 35vw;
	text-align: center;
	font-size: 1.1em;
	border-radius: 1.5vw;
	font-weight: 900;
}

.container__game_item input.text-input {
	width: 100%;
	border: none;
	background: transparent;
	text-align: center;
	font-size: inherit;
	padding: 0;
	margin: 0;
	outline: none;
}

.container__game_item input.text-input:disabled {
	background: transparent;
	color: inherit;
	cursor: not-allowed;
}

.bad {
	color: red;
	max-width: 35vw;
}

.good {
	color: #00b050;
	max-width: 60vw;
}

.container__game_item textarea.text-input {
	font-family: Proxima, sans-serif;
	width: 100%;
	border: none;
	background: transparent;
	text-align: center;
	font-size: inherit;
	padding: 0;
	margin: 0;
	outline: none;
	overflow-wrap: break-word;
	white-space: pre-wrap;
	resize: none;
	overflow: hidden;
}

.container__game_item textarea.text-input:disabled {
	background: transparent;
	color: inherit;
	cursor: not-allowed;
}
</style>
