<script setup>
import { ref } from 'vue'

const props = defineProps({
	fontSize: {
		type: String,
		default: '1em',
	},
	boxShadowHover: {
		type: String,
		default: '0 0 1vw 1vw #fafafa',
	},
	scaleHover: {
		type: String,
		default: '1.05',
	},
})

const isHovered = ref(false)
</script>

<template>
	<button
		:style="{
			fontSize: props.fontSize,
			transform: isHovered ? `scale(${props.scaleHover})` : 'scale(1)',
			boxShadow: isHovered ? props.boxShadowHover : 'none',
		}"
		@mouseenter="isHovered = true"
		@mouseleave="isHovered = false"
	>
		<slot></slot>
	</button>
</template>

<style scoped>
button {
	padding: 1vw 2.5vw;
	background-color: #005bff;
	color: #fafafa;
	border: none;
	transition: transform 0.5s, box-shadow 0.3s;
	cursor: pointer;
}
</style>
